<template>
  <div id="News" @touchstart="refresh()" @touchend="prevent()">
    <Refresh />
    <Title
      title="消息通知"
      back="hidden"
      write="hidden"
      cutDisplay="none"
      finishDisplay="none"
      complainDisplay="none"
      finishDisplay1="none"
      finishDisplay2="none"
      finishDisplay3="none"
    />
    <Connection />
    <smallNews/>
    <div id="empty"></div>
    <Navigation />
  </div>
</template>
<script>
import Refresh from "../components/Public/Refresh.vue";
import Title from "../components/Public/Title.vue";
import Navigation from "../components/Public/Navigation.vue";
import Connection from "../components/News/Connection.vue";
import smallNews from '../components/News/smallNews.vue'
export default {
  name: "News",
  components: {
    Title,
    Navigation,
    Connection,
    Refresh,
    smallNews
  },
  data() {
    return {
      nothing: "",
      another: "",
    };
  },
  mounted() {
    this.changeIcon();
  },
  methods: {
    changeIcon() {
      document.getElementById("Image2").style = "opacity:100%";
    },
    refresh: function () {
      if (window.scrollY == 0) {
        this.another = setTimeout(
          "document.getElementById('Refresh').style.display='flex';",
          500
        );
        this.nothing = setTimeout("location.reload();", 1000);
      }
    },
    prevent: function () {
      if (window.screenY == 0) {
        clearTimeout(this.another);
        clearTimeout(this.nothing);
        document.getElementById("Refresh").style.display = "none";
      }
    },
  },
};
/*
